import React from "react";
import Regra from "./components/Regra";
import Styles from "./Regras.module.css";

const Regras = () => {
  return (
    <section className={Styles.container}>
      <Regra
        title="Oque não é permitido como usuário?"
        text={
          <>
            Não é permitido nada que venha prejudicar a RYCA Store como: <br />
            - Chingamentos
            <br />
            - Reclamações sem motivos
            <br />
            - Ataques
            <br />- Tentativa de burlar o sistema
          </>
        }
      />
      <Regra
        title="Como efetuar uma compra?"
        text={
          <>
            Após carregar sua conta com os métodos de pagamentos disponíveis no
            site, você deverá entrar em loja, escolher um produto e clicar no
            botão ao lado em comprar.
          </>
        }
      />
      <Regra
        title="É permitido estornar um cartão?"
        text={
          <>
            Após uma atualização nós decidimos reduzir os preços das infos e
            desabilitar o sistema de estornos, assim o usuário paga um preço
            mais acessível pela info.
          </>
        }
      />
      <Regra
        title="É possivel fazer o estorno de uma recarga em minha conta? "
        text={<>Nós não fazemos o estorno.</>}
      />
      <Regra
        title="Quais são os preços dos cartão? "
        text={
          <>
            Os preços são variados de acordo com o nível ou bandeira do cartão,
            ele pode variar de 4,50 até 45,00 reais.
          </>
        }
      />
      <Regra
        title="Comprei um cartão e está sem saldo, oque fazer?"
        text={
          <>
            Nesse caso, não nos responsabilizamos por cartão que esteja sem
            saldo, nossa garantia é que o seu cartão esteja bom, e não com saldo
            disponível para realização de sua aprovação. <br />
            Podemos aconselhar guardar o cartão até que a fatura de cartão de
            crédito vire e você tenha mais saldo no mesmo.
          </>
        }
      />
      <Regra
        title="É possivel ver o saldo do cartão?"
        text={
          <>
            Não, o saldo é indeterminado, então pode acontecer de cartões virem
            com pouco saldo ou muito saldo. Sendo a info cc consultada é
            possivel saber o saldo.
          </>
        }
      />
      <Regra
        title="Como escolher um cartão especifico?"
        text={
          <>
            Para escolher um cartão específico você tem as opções para
            classificar os cartões por: bin, level, tipo, bandeira, maior preço,
            menor preço etc.
          </>
        }
      />
    </section>
  );
};

export default Regras;
